@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;font-family:'Poppins', sans-serif}body{position:absolute}

.hcode__main{min-height:calc(100% - 70px);display:-webkit-flex;display:flex;padding:100px 0;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background:url(/hluv/static/media/hentai-bg.530c443e.png) rgba(18,16,19,0.95);background-blend-mode:multiply}.hcode__form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin-top:-40px}.hcode__title{font-size:40px;color:#fff}.hcode__subtitle{font-size:24px;font-weight:500;margin-top:10px;color:#fff}.hcode__input{max-width:630px;text-align:center;font-size:144px;background-color:transparent;border:none;padding:16px 0;margin-bottom:24px;color:#fff}.hcode__input:focus{outline:none}@media (max-width: 425px){.hcode__main{padding:0 16px;background:url(/hluv/static/media/hentai-m-bg.e4044a0a.png) rgba(18,16,19,0.95)}.hcode__subtitle,.hcode__title{text-align:center}.hcode__title{font-size:32px}.hcode__subtitle{font-size:22px;margin-top:20px}.hcode__input{width:100%;font-size:90px;font-family:'Roboto Condensed', sans-serif}}

.navbar{width:100%;height:70px;display:grid;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:0 40px;background-color:#121013}.navbar__language{display:none}@media (max-width: 425px){.navbar{padding:0 20px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.navbar__language{display:none}}

.logo{height:50px}@media (max-width: 425px){.logo{height:40px}}

@-webkit-keyframes spin{from{-webkit-transform:rotate(0);transform:rotate(0)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{from{-webkit-transform:rotate(0);transform:rotate(0)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@-webkit-keyframes pulse{from{-webkit-transform:scale(0.95);transform:scale(0.95)}to{-webkit-transform:scale(1.05);transform:scale(1.05)}}@keyframes pulse{from{-webkit-transform:scale(0.95);transform:scale(0.95)}to{-webkit-transform:scale(1.05);transform:scale(1.05)}}.button{padding:10px 12px;border-radius:5px;font-size:16px;border:none;cursor:pointer}.button-link{text-decoration:none}.button-link,.button-normal{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:16px;font-weight:600}.button-link>*:nth-child(2),.button-normal>*:nth-child(2){margin-left:12px}.button-link__icon,.button-normal__icon{font-size:20px}.button--primary{background-color:#A100F2}.button--primary *{color:white}.button-go{width:265px;padding:10px 0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;border-radius:10px;font-size:40px;color:#fff;font-weight:600;text-decoration:none}.button-go--enabled,.button-go--loading{background-color:#EF1620;box-shadow:0 0 40px rgba(239,22,32,0.76)}.button-go--enabled{cursor:pointer}.button-go--loading{-webkit-animation:pulse 0.66s ease infinite alternate;animation:pulse 0.66s ease infinite alternate;cursor:wait}.button-go--loading>*{-webkit-animation:spin 0.5s ease-in-out infinite forwards;animation:spin 0.5s ease-in-out infinite forwards}.button-go--disabled{background-color:rgba(255,255,255,0.1);color:rgba(255,255,255,0.5);cursor:not-allowed}

.language{width:200px;height:45px;display:-webkit-flex;display:flex;padding:0 12px;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;background-color:rgba(255,255,255,0.05);border:none;border-radius:10px;cursor:pointer}.language:hover>.language__icon{-webkit-transform:rotate(360deg) !important;transform:rotate(360deg) !important;transition:all 0.75s ease-in-out}.language__text{font-size:16px;font-weight:500;color:white}.language__icon{font-size:28px;color:white;-webkit-transform:rotate(0deg) !important;transform:rotate(0deg) !important;transition:all 0.75s ease-in-out}

.flag--small{font-size:24px}.flag--medium{font-size:32px}.flag--large{font-size:40px}

